<template>
  <div >
    <div class="rules__header" v-if="!is404 && (sections[0] || isRemovedPage || popups.addRule.show) && isSearch && !isLoadItems && $store.getters['rules/getItems'].length !== 0  || !isSearch && !is404 && !emptyTopSection || isRemovedPage">
        <div class="text-size-ml text-bold rules__header-wrapper">
          <div v-if="isRemovedPage">
            <span>Удаленные</span>
          </div>
          <div v-else class="rules__header-left">
            <template v-if="isSearch && !isLoadItems && $store.getters['rules/getItems'].length !== 0">
              <div class="text-size-l text-bold search-result">
                <span class="i i-search2 mr8"></span>Результаты поиска «<span class="query" @click="openSearchField">{{searchStr}}</span>»
              </div>
            </template>
            <template v-else>
              <span v-if="topSections[0]" class="ts-name">{{activeTopSectionName}}</span>
              <span class="ss-name" @click="scrollToUp">{{activeSectionName}}</span>
              <span v-if="emptyTopSection && popups.addRule.show && !isLoadSections" class="no-cat" :class="{error: !hasSelectedSection}" @click="scrollToUp">Нет категории</span>
              <span class="rule-removed text-color-red2"  v-if="popups.addRule.show && item.id && !item.active">правило удалено</span>
            </template>
          </div>
          <div class="rules__header-right" v-if="!isSearch">
                <div class="btns" v-if="this.popups.addRule.show">
                      <!--
                          Алексей попросил убрать 4.09.2020
                        <span class="editor-btn has-hint">
                                <span class="i-wrapper">
                                <span class="i i-screen" @click.stop="openAddScreenShotPopup"></span>
                            </span>
                            <span class="hint top-center-direction">Вставить скриншот</span>
                        </span>
                        -->
                        <span class="editor-btn editor-btn__remove" v-if="item.id"  @click.stop="openRemoveRule">
                            <div class="has-hint">
                                <span class="i-wrapper">
                                    <span class="i i-trash6" ></span>
                                    <span class="i i-trash6-active actv" ></span>
                                </span>
                                <span class="hint bottom-center-direction">Удалить правило</span>
                            </div>
                        </span>
                        <span class="editor-btn editor-btn__bold" @click="editor.commands.bold">
                            <div class="has-hint" >
                                <span class="i-wrapper" :class="{ 'is-active': editor.isActive.bold() }">
                                    <span class="i i-bold" v-if="!editor.isActive.bold()"></span>
                                    <span class="i i-bold-active actv" v-else></span>
                                </span>
                                <span class="hint bottom-center-direction">Сделать жирным</span>
                            </div>
                        </span>
                        <span class="editor-btn" @click.stop="openAddVideoPopup">
                            <div class="has-hint">
                                <span class="i-wrapper">
                                    <span class="i i-video2"></span>
                                    <span class="i i-video2-active actv"></span>
                                </span>
                                <span class="hint bottom-center-direction">Вставить видео</span>
                            </div>
                        </span>
                        <span class="editor-btn"  @click.stop="openAddLinkPopup">
                            <div class="has-hint">
                                <span class="i-wrapper">
                                    <span class="i i-link5"></span>
                                    <span class="i i-link5-active actv"></span>
                                </span>
                                <span class="hint bottom-center-direction">Вставить ссылку</span>
                            </div>
                        </span>
                        <span class="editor-btn" @click.stop="openInput()">
                            <div class="has-hint">
                                <span class="i-wrapper" >
                                    <span class="i i-file-plus"></span>
                                    <span class="i i-file-plus-active actv"></span>
                                </span>

                                <span class="hint bottom-center-direction">Вставить файл</span>
                            </div>
                        </span>
                </div>

                <div class="has-hint tumbler__editable" v-if="user.access_rights.is_owner && !isRemovedPage && !( (isLoadSections || isLoadItems) && isSearchPrev) && !popups.addRule.show || (curTopSection && curTopSection.rights == 'W') && !isRemovedPage && !popups.addRule.show && !( (isLoadSections || isLoadItems) && isSearchPrev)">
                    <tumbler
                        :name="tumblers.editable.name"
                        :value="tumblers.editable.value"
                        :icon="'i i-pencil2'"
                    >
                    </tumbler>
                    <span class="hint bottom-center-direction">Режим редактирования</span>
                </div>

                <div class="has-hint tumbler__lock" v-if="popups.addRule.show && user.access_rights.is_owner">
                    <tumbler
                        :name="tumblers.finally.name"
                        :value="tumblers.finally.value"
                        :icon="'i i-lock3'"
                        @change="isChangeRule = true"
                    >
                    </tumbler>
                    <span class="hint bottom-center-direction">Заблокировать редактирование</span>
                </div>

                <div class="has-hint tumbler__notify" v-if="popups.addRule.show">
                    <tumbler
                        :name="tumblers.notify.name"
                        :value="tumblers.notify.value"
                        :icon="'i i-notify'"
                        @change="isChangeRule = true"
                    >
                    </tumbler>
                    <span class="hint bottom-center-direction">Уведомить всех причастных</span>
                </div>

                <div class="rules__btn" @click="openAddRule" v-if="!isRemovedPage && !popups.addRule.show && !isSearch && !( (isLoadSections || isLoadItems) && isSearchPrev)">
                    <span class="elem">
                        <span class="i i-plus6"></span>
                        Добавить правило
                    </span>
                </div>

                <div class="rules__btn" @click="openRemoveRules" v-if="isRemovedPage">
                    <span class="elem">
                        <span class="i i-recycle2"></span>
                        Удалить всё
                    </span>
                </div>


                <div class="rules__btn-yellow btn btn--ico" :class="{'load': save.btnType == 'load', 'rules__btn-disabled':!isChangeRule}" v-if="popups.addRule.show" @click="ruleSend">
                    <svg class="btn-preloader mr4">
                        <use xlink:href="@/assets/img/big-icons-sprite.svg#preloader"></use>
                    </svg>
                    <span class="i i-save3 mr4"></span>
                    Сохранить
                </div>

          </div>
        </div>
    </div>
    <div class="content moderator-rules">
    <div class="nothing-block" v-if="emptyTopSection && !popups.addRule.show && !is404 && !isRemovedPage">
        <div class="nothing-block__wrapper">
            <svg class="big-icon" >
                <use xlink:href="@/assets/img/big-icons-sprite.svg#emoji-nothing"></use>
            </svg>
            <div class="text-size-l text-color-grey mt16">
                В этом разделе нет ни одной категории
            </div>
            <div class="rules__btn mt16" @click="openRuleCategory" v-if="user.access_rights.is_owner || (curTopSection && curTopSection.rights == 'W') && !popups.addRule.show">
                <span class="elem">
                    <span class="i i-plus6"></span>
                    Добавить категорию
                </span>
            </div>
        </div>

        <vue-footer :class="{'footer--center': true}" :show-logo="false" :hide-offer="true" v-if="!popups.addRule.show"></vue-footer>
    </div>

    <div class="nothing-block" v-if="isSearch && !isLoadItems && $store.getters['rules/getItems'].length === 0">
        <div class="nothing-block__wrapper">
            <svg class="big-icon" >
                <use xlink:href="@/assets/img/big-icons-sprite.svg#emoji-nothing"></use>
            </svg>
            <div class="text-size-l text-color-grey mt16">
                По запросу «{{searchStr}}» ничего не найдено
            </div>
                <div class="rules__btn mt16" @click="openSearchField">
                    <span class="elem">
                        Изменить запрос
                    </span>
               </div>
        </div>

        <vue-footer :class="{'footer--center': true}" :show-logo="false" :hide-offer="true" v-if="!popups.addRule.show"></vue-footer>

    </div>
    <div class="nothing-block nothing-block__404" v-if="is404">
        <div class="nothing-block__wrapper">
            <svg class="big-icon" >
                <use xlink:href="@/assets/img/big-icons-sprite.svg#emoji-nothing"></use>
            </svg>
            <div class="text-size-l text-color mt16 text-bold">
                Ошибка 404 – Страница не найдена
            </div>
        </div>

        <vue-footer :class="{'footer--center': true}" :show-logo="false" :hide-offer="true" v-if="!popups.addRule.show"></vue-footer>
    </div>
      <div class="container">
        <aside-sticky :marginTop="112" :marginBottom="32" v-if="!isRemovedPage && !isSearch && !is404" :key="isRemovedPage">
          <aside class="aside aside--left aside--catalog" >
            <rules-sections v-if="topSections[0]"></rules-sections>
            <span class="text-color-grey no-cat-text" :class="{error: !hasSelectedSection}" v-if="emptyTopSection && !isLoadSections && popups.addRule.show" :key="isLoadSections">
                Чтобы добавить правило в этот<br>
                раздел, сначала создайте в нём<br>
                хотя бы одну категорию
            </span>
          </aside>
        </aside-sticky>
        <div class="container__full container-scroll container__right" :class="{'container__rule-edit': popups.addRule.show, 'container__removed-page': isRemovedPage || isSearch}" v-if="sections[0] || isRemovedPage || popups.addRule.show">
          <add-rule @change="isChangeRule = true" @load="saveLoading"  @scroll="setScrollId" :show="popups.addRule.show" v-if="popups.addRule.show" :editor="editor" :focused="focused" :inputText="inputText" @close="removeContent" ref="addRule"></add-rule>
          <rules-list @openeditrule="isChangeRule = false" v-else-if="(sections[0] || isSearch) && !popups.addRule.show || isRemovedPage && !popups.addRule.show" :isErrorPage="is404"></rules-list>

          <vue-footer :class="{'footer--center': true}" :show-logo="false" :hide-offer="true" v-if="!popups.addRule.show"></vue-footer>
        </div>
      </div>

      <editor-menu-bar :editor="editor" v-slot="{ commands }" v-if="popups.addRule.show">
        <div>
          <popup :show="popups.addScreenShot.show" :name="popups.addScreenShot.name" @close="screenShotLink.value = ''" :width="400">
            <div>
              <div class="text-size-l text-bold text-color mb24">Вставить скриншот</div>
              <div class="full">
                <input-styled
                    :auto-focus="true"
                    ref="screenShotLink"
                    name="category"
                    :placeholder="'Название категории'"
                    :placeholderInput="'ССЫЛКА LIGHTSHOT ИЛИ YANDEX'"
                    @enter="addScreenShot(commands)"
                    v-model="screenShotLink.value"
                    @focus="screenShotLink.error=false"
                    :error-text="screenShotLink.value.length > 0 ? 'Для скриншотов используйте сервисы lightshot или yandex' : ''"
                    :is-error="screenShotLink.error">
                </input-styled>
              </div>
              <div class="row-b mt24">
                <div class="row-b__col elem-pos-fix mr24">
                  <submit-btn class="btn" ref="screenShotLinkBtn" @click.native="addScreenShot(commands)" :type="screenShotLink.btnType">
                    <span class="i i-ok7 mr8" v-if="screenShotLink.btnType === 'normal'"></span>Готово
                  </submit-btn>
                </div>
              </div>
            </div>
          </popup>
          <popup :show="popups.addLink.show" :name="popups.addLink.name" :width="400">
        	<div>
        		<div class="text-size-l text-bold text-color mb24">Вставить ссылку</div>
        		<div class="full">
        			<input-styled
        				:auto-focus="true"
        				name="category"
        				:placeholder="''"
        				:placeholderInput="'ССЫЛКА'"
        				@enter="setLinkUrl(commands)"
        				v-model="link.value"
        				@focus="link.error=false"
        				:error-text="link.value.length > 0 ? 'Используйте ссылку':''"
        				:is-error="link.error">
        			</input-styled>
        		</div>
        		<div class="row-b mt24">
        			<div class="row-b__col elem-pos-fix mr24">
        				<submit-btn class="btn" @click.native="setLinkUrl(commands)"
        							:type="link.btnType">
        					<span class="i i-ok7 mr8" v-if="link.btnType === 'normal'"></span>Готово
        				</submit-btn>
        			</div>
        		</div>
        	</div>
        </popup>
          <popup :show="popups.addVideo.show" :name="popups.addVideo.name" :width="400">
            <div>
              <div class="text-size-l text-bold text-color mb24">Вставить видео</div>
              <div class="full">
                <input-styled
                    :auto-focus="true"
                    name="category"
                    :placeholder="''"
                    :placeholderInput="'ССЫЛКА YOUTUBE'"
                    @enter="addScreenShot(commands)"
                    v-model="screenShotLink.value"
                    @focus="screenShotLink.error=false"
                    :error-text="screenShotLink.value.length > 0 ? 'Используйте ссылку на видео с YouTube':''"
                    :is-error="screenShotLink.error">
                </input-styled>
              </div>
              <div class="row-b mt24">
                <div class="row-b__col elem-pos-fix mr24">
                  <submit-btn class="btn" @click.native="addScreenShot(commands)" :type="screenShotLink.btnType">
                    <span class="i i-ok7 mr8" v-if="screenShotLink.btnType === 'normal'"></span>Готово
                  </submit-btn>
                </div>
              </div>
            </div>
          </popup>
          <input multiple style="opacity: 0;position: absolute; width: 0;" ref="fileInput" @change="onFileAppendEvent(commands, $event)" type="file">
        </div>
      </editor-menu-bar>

      <popup :is-set-styled="false" :show="popups.ruleCategory.show" :name="popups.ruleCategory.name" :width="400" :add-class="'add-cat add-cat-uppercase'">
        <rule-category></rule-category>
      </popup>

      <popup :is-set-styled="false" :show="popups.removeRule.show" :name="popups.removeRule.name" :width="512" >
        <remove-rule></remove-rule>
      </popup>

      <popup :is-set-styled="false" :show="popups.removeRuleForever.show" :name="popups.removeRuleForever.name" :width="512" >
        <remove-rule-forever></remove-rule-forever>
      </popup>

      <popup :is-set-styled="false" :show="popups.removeRules.show" :name="popups.removeRules.name" :width="512" >
        <remove-rules></remove-rules>
      </popup>

      <popup :is-set-styled="false" :show="popups.errorScreenUpload.show" :name="popups.errorScreenUpload.name" :width="512" >
        <error-upload></error-upload>
      </popup>

      <popup :is-set-styled="false" :show="popups.removeCatError.show" :name="popups.removeCatError.name" :width="400" :is-btn-close="false">
        <remove-cat-error></remove-cat-error>
      </popup>

      <!-- В данный момент редактирования недоступно -->
      <popup :is-set-styled="false" :show="popups.editDisabled.show" :name="popups.editDisabled.name" :width="512" :is-btn-close="false">
        <edit-disabled></edit-disabled>
      </popup>

      <!-- В данный момент редактирования недоступно -->
      <popup :is-set-styled="false" :show="popups.editAdminDisabled.show" :name="popups.editAdminDisabled.name" :width="512" :is-btn-close="false">
        <edit-admin-disabled></edit-admin-disabled>
      </popup>

    </div>
  </div>
</template>


<script>
import {mapState} from 'vuex';
import AsideSticky from '@/components/AsideSticky.vue';
import Gallery from '@/components/Gallery.vue';
import Popup from '@/components/modal/Popup.vue';
import RuleCategory from '@/components/modal/rules/RuleCategory.vue';
import AddRule from '@/components/modal/rules/AddRule.vue';
import Tumbler from '@/components/Tumbler.vue';
import RemoveRule from '@/components/modal/rules/RemoveRule.vue';
import RemoveRuleForever from '@/components/modal/rules/RemoveRuleForever.vue';
import ErrorUpload from '@/components/modal/rules/ErrorUpload.vue';
import RemoveRules from '@/components/modal/rules/RemoveRules.vue';
import RecoverRule from '@/components/modal/rules/RecoverRule.vue';
import RemoveCatError from '@/components/modal/rules/RemoveCatError.vue';
import EditDisabled from '@/components/modal/rules/EditDisabled.vue';
import EditAdminDisabled from '@/components/modal/rules/EditAdminDisabled.vue';
import RulesSections from '@/components/moderator/Rules/Sections.vue';
import RulesList from '@/components/moderator/Rules/List.vue';
import VueFooter from '@/components/VueFooter.vue';
import InputStyled from '@/components/forms/InputStyled.vue';
import TextareaField from '@/components/forms/TextareaField.vue';
import InputSelect from '@/components/forms/InputSelect.vue';
import SubmitBtn from '@/components/forms/SubmitBtn.vue'
import TextEditorNew from '@/components/editor/TextEditorNew.vue'
import draggable from '@/helpers/vuedraggable.js';
import {Hooper, Slide, Navigation as HooperNavigation} from 'hooper'; // carousel
import {Editor, EditorContent, EditorMenuBar, Extension} from 'tiptap';
import InputSelectNew from '@/components/forms/InputSelectNew.vue';
import {
    Placeholder,
    History,
    Bold,
} from 'tiptap-extensions'
import {
    Text,
    Doc,
} from 'tiptap'
import ScreenShot from '@/components/editor/tiptop/screenshot.js';
import {scrollTo} from '@/helpers/animate.js';
import Youtube from '@/components/editor/tiptop/youtube.js';
import HardBreak from '@/components/editor/tiptop/HardBreak.js';
import OtherFile from '@/components/editor/tiptop/OtherFile.js';
import ImagePreloader from '@/components/editor/tiptop/ImagePreloader.js';
import PasteText from '@/components/editor/tiptop/PasteText.js';
import Paragraph from '@/components/editor/tiptop/Paragraph.js';
import Link from '@/components/editor/tiptop/Link.js';
import { Slice } from 'prosemirror-model'

export default {
    metaInfo() {
        return {
            title: 'Правила',
            titleTemplate: this.$store.state.options.titleTemplate
        }
    },
    name: "Rules",
    components: {
        AsideSticky,
        Gallery,
        RuleCategory,
        AddRule,
        RulesSections,
        RulesList,
        RemoveRule,
        RemoveRuleForever,
        RemoveCatError,
        RemoveRules,
        VueFooter,
        InputStyled,
        TextareaField,
        InputSelect,
        SubmitBtn,
        draggable,
        RecoverRule,
        Hooper,
        Slide,
        HooperNavigation,
        ErrorUpload,
        TextEditorNew,
        EditorContent,
        EditorMenuBar,
        Popup,
        InputSelectNew,
        EditDisabled,
        EditAdminDisabled,
        Tumbler,
    },
    data: function () {
        return {
            focus: false,
            focused: false,
            search: '',
            isShowedViewer: false,
            inputText: '',
            currentSlide: 1,
            tryScroll: 0,
            screenShotLink: {
                value: "",
                error: false,
                btnType: 'normal',
            },
            link: {
                value: "",
                error: false,
                btnType: 'normal',
            },
            save: {
                btnType: 'normal',
            },
            scrollID: false,
            isChangeRule: false,
            editor: new Editor({
                extensions: [
                    new HardBreak(),
                    new Text(),
                    new Paragraph(),
                    new Doc(),
                    new History(),
                    new ScreenShot(),
                    new Youtube(),
                    new OtherFile(),
                    new ImagePreloader(),
                    new PasteText(),
                    new Link(),
                    new Bold(),
                    new Placeholder({
                        emptyEditorClass: 'is-editor-empty',
                        emptyNodeClass: 'is-empty',
                        emptyNodeText: 'Подробно опишите правило',
                        showOnlyWhenEditable: false,
                        showOnlyCurrent: false,
                    }),
                ],
                useBuiltInExtensions: false,
                onUpdate: ({getJSON, getHTML}) => {
                    this.divInput(getHTML())
                },
                onFocus: () => {
                    this.divFocus();
                },
                onBlur: () => {
                    this.divBlur();
                },
                onPaste: (view, event, slice) => {
                    let sliceJson = slice.toJSON();
                    if(sliceJson){
                        let newContent = {
                            content: [],
                        };
                        if(sliceJson.openEnd){
                            newContent.openEnd = sliceJson.openEnd;
                        }
                        if(sliceJson.openStart){
                            newContent.openStart = sliceJson.openStart;
                        }
                        for (let c in sliceJson.content) {
                            let item = sliceJson.content[c];
                            if(item.type == 'paragraph'){
                                for (let p in item.content) {
                                    newContent.content.push(item.content[p])
                                }
                                newContent.content.push({type: 'hard_break'})
                            } else {
                                newContent.content.push(item)
                            }
                        }
                        for (let s in newContent.content) {
                            if(newContent.content[s].type == 'text' ){
                                if(newContent.content[s].text.trim() == ''){
                                    newContent.content.splice(s,1);
                                } else {
                                    newContent.content[s].text = newContent.content[s].text;
                                }
                            }
                        }

                        let countBr = 0;
                        let contentClearBr = [];
                        for (let s in newContent.content) {
                            if(newContent.content[s].type == 'hard_break'){
                                countBr++;
                                if(countBr < 3 && s < newContent.content.length - 1){
                                    contentClearBr.push(newContent.content[s])
                                }
                            } else {
                                contentClearBr.push(newContent.content[s])
                                countBr = 0;
                            }
                        }

                        if(contentClearBr.length == 1){
                            newContent.openStart = 0;
                            newContent.openEnd = 0;
                        }
                        if(contentClearBr.length > 0){
                            if(view.state.selection.ranges[0].$from.pos == 0){
                                newContent.content = [{
                                    content: contentClearBr,
                                    type: 'paragraph'
                                }]
                            } else {
                                newContent.content = contentClearBr;
                            }
                            let newSlice = Slice.fromJSON(this.editor.schema, newContent)
                            slice.content = newSlice.content
                        }
                    }
                    this.pasteEditor(event)
                    return true;
                },
            }),

    }
  },
  computed: {
    ...mapState({
      popups: state => state.popups.items,
      sections: state => state.rules.sections,
      rolled: state => state.rules.rolled,
      topSections: state => state.rules.topSections,
      isLoadItems: state => state.rules.isLoadItems,
      isLoadSections: state => state.rules.isLoadSections,
      hasSelectedSection: state => state.rules.hasSelectedSection,
      canDragState: state => state.rules.canDrag,
      item: state => state.rules.editItem,
      isSearch: state => state.rules.isSearch,
      isSearchPrev: state => state.rules.isSearchPrev,
      user: state => state.users.user,
      searchStr: state => state.rules.searchStr,
      first404: state => state.rules.first404,
      tumblers: state => state.rules.tumblers,
    }),
      canDrag(){
          if(this.user.access_rights.is_owner || (this.curTopSection && this.curTopSection.rights == 'W')) {
              return this.canDragState
          } else {
              return false
          }
      },
    activeSection(){
      return this.$route.params.id ? this.$route.params.id : this.sections[0].id;
    },
      emptyTopSection(){
        return ((typeof this.sections == 'object' && this.sections.length == 0) || !this.sections) && !this.isLoadSections
      },
    activeTopSectionName(){
      let name = '';
      if(this.$route.params.id || this.activeTopSection){
        let section = this.topSections.find(item => item.id == this.activeTopSection);
        if(section){
          name = section.name;
        }
      }
      return name;
    },
    activeTopSectionSubSectionName(){
      let name = '';
      if(this.$route.params.id || this.activeTopSection){
        let section = this.topSections.find(item => item.id == this.activeTopSection);
        if(section){
          name = section.subtitle;
        }
      }
      return name;
    },
    activeTopSection(){
      if(this.isSearch || !this.topSections.length) return false;
      return this.$route.params.id ? this.$route.params.id : this.topSections[0].id;
    },
    activeSectionName(){
      if(!this.isLoadSections){
        if(this.$route.params.subid && this.sections){
          let sections = this.sections.find(item => item.id == this.$route.params.subid)
          return sections ? sections.name : '';
        }else if(this.sections[0]){
          let sections =this.sections.find(item => item.id == this.sections[0].id);
          return sections ? sections.name : '';
        }else{
          return '';
        }
      } else {
        return this.activeTopSectionSubSectionName;
      }
    },
    activeSubSection(){
      if(this.$route.params.subid && !this.isRemovedPage){
        return this.$route.params.subid;
      }else if(this.sections[0] && !this.isRemovedPage){
        return this.sections[0].id;
      }else{
        return '';
      }
    },
    is404() {
      let topSection = this.topSections.find(sect => sect.id === this.$route.params.id);
      let subSectionId = false;

      if (this.$route.params.id) {
        if (topSection) {
          if (this.$route.params.subid) {
            subSectionId = topSection.subsections.find(id => id === this.$route.params.subid);
          } else {
            subSectionId = true;
          }
        }
      } else {
        subSectionId = true;
      }

      //пробуем получить данные, чтобы вызвать ошибку доступа, можно и без запроса, но так надёжнее
      //пока без запроса так
      let noSuccess = false;
      if (typeof this.topSections[0] !== 'undefined') {

        if (!subSectionId && this.$route.params.id) {
          if (typeof this.topSections[0].all[this.$route.params.id] !== 'undefined') {
            if (this.$route.params.subid) {
              if (typeof this.topSections[0].all[this.$route.params.id].find(id => id === this.$route.params.subid) !== 'undefined') {
                noSuccess = true;
              }
            } else {
              noSuccess = true;
            }
          } else {
            noSuccess = false;
          }
        }

      }

      return (this.$route.name === 'page404' || !subSectionId) && !noSuccess && !this.isLoadItems && !this.isLoadSections;
      //return (((this.$route.name === 'page404' || this.activeTopSectionName === '') && this.topSections.length > 0) || (this.activeTopSectionName !== '' && !subSectionId && this.sections.length > 0)) && !this.isSearch;
    },
    files: {
      /*if(this.item.files && this.item.files.length>0){
          return this.item.files.concat(this.form.tmpFiles)
      }
      else
          return this.form.tmpFiles;*/
      get(){
        return this.$store.getters['rules/getItemFiles']
      },
      set(value){
        this.$store.commit('rules/setItemFiles', {
          items: value
        });
      }
    },
    isAddRuleShowed(){
      return this.popups.addRule.show
    },
    curTopSection (){
      if(!this.topSections[0]) return false;

      for (let i in this.topSections) {
        if(this.topSections[i].id == this.activeTopSection){
          return this.topSections[i];
        }
      }
    },
    isRemovedPage (){
      return this.$route.name == 'rules-removed';
    }
  },
  watch: {
    activeSubSection(val, oldVal){
        this.isChangeRule = true;
    }
  },
  methods:{
    setLinkUrl(command){
        if (this.link.value == '') {
            this.link.error = true;
        } else {
            command.link({ href: this.link.value ? this.link.value : null, target: '_blank' });
            this.link.value = '';
            this.$store.commit('popups/close', {name: this.popups.addLink.name});
        }
    },
    openAddLinkPopup() {
        this.screenShotLink.value = '';
        this.$store.commit('popups/open', {name: this.popups.addLink.name});
    },
    saveLoading(load){
        this.save.btnType = load;
    },
    openSearchField: function () {
      setTimeout(() => {
        let searchFieldInput = document.getElementById('searchFieldInput');
        this.$store.commit('rules/openSearchField', true);
        searchFieldInput.focus();
      }, 100);
    },
    scrollToUp(){
      scrollTo('up');
      this.toggleRoll();
    },
    setScrollId(id){
      this.scrollID = id;
    },
      scrollToElement() {
          this.$nextTick(() => {
              let id;
              if (this.scrollID && this.scrollID != 'new') {
                  id = 'rule' + this.scrollID;
              } else if (this.scrollID == 'new') {
                  id = document.querySelector('.rules').lastElementChild.id;
              }
              if (!document.getElementById(id) && this.tryScroll < 5 ) {
                  this.tryScroll++;
                  setTimeout(() => {
                      this.scrollToElement();
                  }, 100);

              } else {
                  if (this.scrollID) {
                      if (this.scrollID && this.scrollID != 'new') {
                          if (document.getElementById(id)) {
                              let items = this.$store.getters['rules/getItems'];
                              let idx = items.findIndex(el => el.id == this.scrollID);
                              let item = items.find(el => el.id == this.scrollID);

                              this.$store.dispatch('rules/getRuleText', {item: item, index: idx}).then(() => {
                                  let height = document.getElementById(id).querySelector('.rule__text').clientHeight;
                                  document.getElementById(id).querySelector('.rule__text-container').style.maxHeight = document.getElementById(id).classList.contains('opened') ? '17px' : height + 'px';
                                  this.$store.commit('rules/toggleRule', idx);
                                  this.$store.dispatch('rules/deleteMarks', {id: this.scrollID});
                              });
                          }

                      } else if (this.scrollID == 'new') {
                          id = document.querySelector('.rules').lastElementChild.id;
                          let items = this.$store.getters['rules/getItems'];
                          let idx = items.length - 1;
                          let item = items[idx];

                          this.$store.dispatch('rules/getRuleText', {item: item, index: idx}).then(() => {
                              let height = document.getElementById(id).querySelector('.rule__text').clientHeight;
                              document.getElementById(id).querySelector('.rule__text-container').style.maxHeight = document.getElementById(id).classList.contains('opened') ? '17px' : height + 'px';
                              if (!this.scrollID || this.scrollID == 'new') {
                                  this.$store.commit('rules/toggleRule', idx);
                              }
                              this.$store.dispatch('rules/deleteMarks', {id: this.scrollID});
                          });
                      }
                      scrollTo('#' + id);
                  }
                  this.scrollID = false;
                  this.tryScroll = 0;
              }

          })
      },
    removeContent(){
      this.editor.setContent('');
    },
    divFocus(){
      this.focused = true;
    },
    divBlur(){
      this.focused = false;
    },
    divInput(e){
        if(this.inputText != e){
            this.isChangeRule = true;
        }
        this.inputText = e;
    },
      pasteEditor(event) {
          let items = (event.clipboardData || event.originalEvent.clipboardData).items;
          let files = [];
          let isMixPaste = false;
          let text = false;
          for (let index in items) {
              let item = items[index];
              if ((item.kind == 'string') && (item.type.match('^text/plain'))) {
                  // This item is the target node
                  /*item.getAsString( (s)=>{
                      let text = s.trim().split('\n');
                      //console.log(s)
                      for (let t in text) {
                          if(text[+t+1] == '' && text[t] == ''){

                          } else {
                              this.editor.commands.pasteText({text:text[t].trim()});
                              if(t < text.length - 1){
                                  this.editor.commands.pasteBr();
                              }
                          }
                      }
                  });*/
                  isMixPaste = true;
              } else if ((item.kind == 'string') && (item.type.match('^text/html'))) {
                  item.getAsString((s)=>{
                      //console.log(s)
                  });
                  isMixPaste = true;
              } else if ((item.kind == 'string') && (item.type.match('^text/uri-list'))) {
                  item.getAsString((s)=>{
                  });
                  isMixPaste = true;
              } else if ((item.kind == 'file')) {
                  let blob = item.getAsFile();
                  files.push(blob);

              }
          }
          if(!isMixPaste && files){
              this.onFileAppend(this.editor.commands, files);
          }

          return false;
      },
    openAddRule: function () {
        this.isChangeRule = false;
        this.$store.dispatch('rules/setEditItem',[]).then(()=>{
        this.$store.dispatch('popups/open', {name: this.popups.addRule.name});
        this.$store.commit('rules/setPreviousSection', {topSection: this.activeTopSection, section: this.activeSubSection});

      })
      scrollTo('up');
    },
    openRemoveRules: function () {
      this.$store.dispatch('popups/open', {name: this.popups.removeRules.name});
    },
    resetSearch: function () {
      this.search= '';
      this.fetchData();
    },
    fetchData () {
      this.$store.dispatch('rules/getItems', {id: this.activeSubSection, search: this.search, reload: true});
    },
    toggleRoll(){
      this.$store.commit('rules/toggleRolled');
    },
    toggleDrag(){
      this.$store.commit('rules/toggleDrag');
    },
    getExt: function (name) {
      let parts;
      return (parts = name.split(".")).length > 1 ? parts.pop().toLowerCase() : "";
    },
      onFileAppendEvent(command, e){
          let files = e.target.files || e.dataTransfer.files;
          if (!files.length)
              return;
          this.onFileAppend(command, files);
      },
      onFileAppend: function (command, files) {
      if (!files.length)
        return;
      let error = false;
      for (let i in files) {
        if(!files[i].size){
          continue;
        }
        let ext = this.getExt(files[i].name);

                if (!(files[i].size < 10 * 1024 * 1024 && (ext === 'jpg' || ext === 'jpeg' || ext === 'png' || ext === 'doc' || ext === 'docx' || ext === 'pdf' || ext === 'xls' || ext === 'xlsx'))) {
                    error = true;
                }
            }
            if (error) {
                this.$store.commit('popups/open', {name: this.popups.errorScreenUpload.name});
            } else {
                this.loadFiles = true;

                for (let f in files) {
                    let filesId = [];
                    if (!files[f].size) {
                        continue;
                    }
                    let params = {};
                    let formData = new FormData();
                    formData.append('type', 'uploadFiles');
                    formData.append('files_' + f, files[f]);
                    let id = Math.random().toString(36).substr(2, 9);
                    filesId.push({
                        id: id
                    })
                    command.image_preloader({'data-id': id})
                    params.formData = formData;

                    this.$store.dispatch('rules/uploadFiles', params).then((f) => {
                        this.loadFiles = false;
                        for (let i in f) {
                            if (f[i].path) {
                                let ext = this.getExt(f[i].name);
                                let preload = filesId[i] ? filesId[i].id : false;
                                if (ext === 'jpg' || ext === 'jpeg' || ext === 'png') {
                                    command.screenshot({
                                        'data-href': 'https://rules.su/' + f[i].path,
                                        'data-id': f[i].id,
                                        'data-small': 'https://rules.su/' + f[i].small,
                                        'preload': preload
                                    })
                                } else {
                                    command.other_file({
                                        'data-href': 'https://rules.su/' + f[i].path,
                                        'data-id': f[i].id,
                                        'data-type': ext,
                                        'preload': preload
                                    })
                                }
                            }
                        }
                    });
                }

            }

        },
      ruleSend(){
        if(this.isChangeRule) {
            this.$refs.addRule.send();
        }
      },
      openRuleCategory: function () {
          this.$store.dispatch('popups/open', {name: this.popups.ruleCategory.name});
      },
        openAddScreenShotPopup() {
            this.screenShotLink.value = '';
            this.$store.commit('popups/open', {name: this.popups.addScreenShot.name});
        },

        openAddVideoPopup() {
            this.screenShotLink.value = '';
            this.$store.commit('popups/open', {name: this.popups.addVideo.name});
        },
      openRemoveRule: function () {
          //this.$store.commit('popups/close', {name: this.popups.addRule.name});
          if(this.isRemovedPage || this.item.id && !this.item.active){
              this.$store.commit('popups/open', {name: this.popups.removeRuleForever.name});
          }
          else{
              this.$store.commit('popups/open', {name: this.popups.removeRule.name});
          }
      },
        addScreenShot(command) {
            if (this.screenShotLink.value == '' || this.screenShotLink.value != '' && this.screenShotLink.value.match(/prntscr.com|yadi.sk|prnt.sc|youtu(?:be\.com\/watch\?v=|\.be\/)/) === null) {
                this.screenShotLink.error = true;
            } else {
                let ids = this.screenShotLink.value.match(/http(?:s?):\/\/(?:www\.)?youtu(?:be\.com\/watch\?v=|\.be\/)([\w\-\_]*)(&(amp;)?[\w\?=]*)?/);
                if (ids && ids[1]) {
                    let id = ids[1]
                    command.youtube({'data-id': id});
                    this.$store.commit('rules/addFile', {id: id, mediaType: 'video'});
                    this.$store.commit('popups/close', {name: this.popups.addVideo.name});
                    this.screenShotLink.value = '';
                    this.screenShotLink.btnType = 'normal';
                } else {
                    this.screenShotLink.btnType = 'load';
                    this.$store.dispatch('rules/addScreenShot', {value: this.screenShotLink.value}).then((src) => {
                        this.screenShotLink.btnType = 'ok';
                        if (src.path) {
                            command.screenshot({
                                'data-href': 'https://rules.su/' + src.path,
                                'data-id': src.id,
                                'data-small': 'https://rules.su/' + src.small
                            })
                        }
                        setTimeout(() => {
                            this.$store.commit('popups/close', {name: this.popups.addScreenShot.name});
                            this.screenShotLink.value = '';
                            this.screenShotLink.btnType = 'normal';
                        }, 200);
                    });
                }

            }
        },
        openInput: function () {
            this.$refs.fileInput.click();
        },
        keyUp: function (e) {
            /*if (this.$refs.screenShotLinkBtn) {
              if ((e.ctrlKey || e.metaKey) && e.code === 'KeyV') {
                this.$refs.screenShotLinkBtn.$el.click();
              }
            }*/
        }
    },
    mounted() {
        this.$store.subscribe((mutation, state) => {
            if (mutation.type == 'rules/setIsSearch') {
                if (state.rules.isSearch === false) {
                    this.search = '';
                }
            }
            if (mutation.type == 'rules/setItems') {

                this.scrollToElement()
            }
        });

        document.addEventListener('keyup', this.keyUp);
    },
    beforeDestroy() {
        document.removeEventListener('keyup', this.keyUp);
    }
}
</script>
