import { Node } from 'tiptap'

export default class Youtube extends Node {

    get name() {
        return 'youtube'
    }

    get schema() {
        return {
            attrs: {
                'data-id': {
                    default: null,
                },
            },
            inline: true,
            group: 'inline',
            selectable: true,
            draggable: true,
            parseDOM: [{
                tag: 'span.youtube',
                getAttrs: dom => ({
                    'data-id': dom.getAttribute('data-id'),
                }),
            }],
            toDOM: node => ['span', {
                'class': 'youtube',
                'data-id': node.attrs['data-id'],
                'style': 'background-image: url('+'youtubeimg' + node.attrs['data-id'] + '/maxresdefault.jpg'+');',
                'clickprevent': '$emit(show,\''+node.attrs['data-id']+'\')',
            }],
        }
    }

    get view() {
        return {
            props: ['node', 'updateAttrs', 'view'],
            computed: {
                src: {
                    get() {
                        return this.node.attrs['data-id']
                    },
                    set(src) {
                        this.updateAttrs({
                            src,
                        })
                    },
                },
                id(){
                    return this.node.attrs['data-id'];
                },
                preview() {
                    return '//img.youtube.com/vi/' + this.id + '/maxresdefault.jpg'
                },
            },
            methods: {
                showFullScreen(id) {
                    document.dispatchEvent(new CustomEvent('showvideo', { detail: id }));
                },
            },
            template: `<span class="youtube" @click="showFullScreen(id)" :style="{'background-image': 'url('+preview+')'}">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>`,
        }
    }

    commands({ type }) {
        return {
            youtube: attrs => (state, dispatch) => {
                const { selection } = state
                const position = selection.$cursor ? selection.$cursor.pos : selection.$to.pos
                const node = type.create(attrs)
                const transaction = state.tr.insert(position, node)
                dispatch(transaction)
            },
        }
    }

}