<template>
    <div>
        <component @open="open" @show="showFullScreen" :is="compiledData"></component>
        <div v-if="opened && files != []" class="viewer-container viewer-backdrop viewer-fixed viewer-hooper viewer-fade" ref="viewer"
             @keyup.space="slideNext" @keyup.esc="closeViewer">

            <div class="viewer-canvas-wrapper">

                <div class="viewer-canvas" :class="{'nav-hovered-prev': hoveredPrev, 'nav-hovered-next': hoveredNext}">
                    <hooper @slide="slide" class="carousel-thumbs viewer-carousel" ref="viewerHooper" :infiniteScroll="true">

                        <slide :key="index"
                               v-for="(item,index) in files">
                          <div class="item" v-if="item.mediaType == 'photo'">
                              <img v-lazy="item.path" alt=""  v-if="files != [] && (currentSlide == index+1 || currentSlidePrev == index) && isShowedViewer">
                              <div class="inner-nav" v-if="files.length > 1">
                                  <div class="prev" @click="slidePrev" @mouseover="hoveredPrev = true" @mouseleave="hoveredPrev = false"></div>
                                  <div class="next" @click="slideNext"  @mouseover="hoveredNext = true" @mouseleave="hoveredNext = false"></div>
                              </div>
                          </div>
                          <div class="item" v-else>
                            <iframe width="1100"
                                    v-if="files != [] && (currentSlide == index+1 || currentSlidePrev == index) && isShowedViewer"
                                    height="450"
                                    :src="'https://www.youtube.com/embed/'+item.id+'?enablejsapi=1'"
                                    frameborder="0"
                                    allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
                                    allowfullscreen></iframe>
                                    <div class="inner-nav" v-if="files.length > 1">
                                        <div class="prev" @click="slidePrev" @mouseover="hoveredPrev = true" @mouseleave="hoveredPrev = false"></div>
                                        <div class="next" @click="slideNext"  @mouseover="hoveredNext = true" @mouseleave="hoveredNext = false"></div>
                                    </div>
                          </div>
                        </slide>
                        <hooper-navigation slot="hooper-addons" v-if="files.length > 1"></hooper-navigation>
                    </hooper>
                </div>
                <div role="button" class="viewer-button viewer-close" @click="closeViewer"></div>
                <div class="photo-num">
                    {{currentSlide}}
                </div>
            </div>
        </div>
    </div>

</template>

<script>
    import {Hooper, Slide, Navigation as HooperNavigation} from 'hooper'; // carousel

    export default {
        name: "DynamicHtml",
        components: {
            Hooper,
            Slide,
            HooperNavigation,
        },
        props:{
            html: {
               default: ''
            },
            opened: {
               default: false
           }
        },
        data () {
            return {
                currentSlide: 1,
                currentSlidePrev: 1,
                hoveredPrev: false,
                hoveredNext: false,
                isShowedViewer: false,
            }
        },
        computed: {
            compiledData () {
                let html = this.html.replace(/clickprevent/g,'@click.prevent').replace(/youtubeimg/g,'//img.youtube.com/vi/').replace(/\$emit\(show/g,'$emit(\'show\'').replace(/\$emit\(open/g,'$emit(\'open\'');
                return {
                    template: `<div>${html}</div>`
                }
            },
            files(){
                let result = this.html.match(/<span[\s\S]*?class="(image|youtube)"[\s\S]*?\/span>/gi);
                let IDs = [];
                let files = [];
                for(let r in result){
                    let div = document.createElement('div');
                    div.innerHTML = result[r];
                    let type;
                    let element;
                    if(div.querySelector("span.image")){
                        element = div.querySelector("span.image");
                        type = 'image';
                    }
                    if(div.querySelector("span.youtube")){
                        element = div.querySelector("span.youtube");
                        type = 'youtube';
                    }
                    //if(element && IDs.indexOf(element.dataset.id) === -1){
                        IDs.push(element.dataset.id);
                        if(type === 'image'){
                            let href = element.dataset.href;
                            files.push({id:element.dataset.id,path:href,icon:href,mediaType: 'photo'});
                        }
                        if(type === 'youtube'){
                            let id = element.dataset.id;
                            let path = '//img.youtube.com/vi/' + id + '/maxresdefault.jpg'
                            files.push({id: id, path: path, icon: path, mediaType: 'video'});
                        }
                    //}

                }
                return files;
            }
        },
        methods: {
            slide(payload){
                this.currentSlide = +payload.currentSlide == -1 ? this.files.length : +payload.currentSlide +1;
                if(this.files && +this.currentSlide > this.files.length){
                    this.currentSlide = 1;
                }
                this.currentSlidePrev = +payload.slideFrom
                this.pauseVideos();
            },
            loaded(e){
              console.log('loaded')
            },
            showFullScreen(id){
                for(let i in this.files){
                    if(this.files[i].id == id){
                        this.showViewer(i);
                        break;
                    }
                }
            },
            open(href) {
                let  link = document.createElement('a');
                link.setAttribute('href',href);
                link.setAttribute('download','download');
                link.setAttribute('target','_blank');
                link.click();
            },
            pauseVideos() {
                [...document.getElementsByTagName('iframe')].forEach(i => i.contentWindow.postMessage('{"event":"command","func":"pauseVideo","args":""}', '*'));
            },
            closeViewer() {
                this.pauseVideos();
                if (this.isShowedViewer) {
                    this.$refs.viewer.classList.add('viewer-transition');
                    this.$refs.viewer.classList.remove('viewer-in');
                    document.getElementsByTagName('body')[0].classList.remove("popup-noscroll");
                    let self = this;
                    setTimeout(function () {
                        if (self.$refs.viewer) {
                            self.$refs.viewer.classList.remove('viewer-transition');
                        }
                        if (self.$refs.viewerHooper) {
                            self.$refs.viewerHooper.restart();
                        }
                        self.isShowedViewer = false;
                    }, 200);
                }
            },
            showViewer(index) {
                this.$refs.viewer.classList.add('opening');
                this.$refs.viewerHooper.slideTo(index);
                this.$refs.viewer.classList.add('viewer-transition');
                this.$refs.viewer.querySelector('.hooper-track').setAttribute('tabindex', 0);
                let self = this;
                setTimeout(function () {
                    self.$refs.viewer.classList.remove('viewer-transition');
                    self.$refs.viewer.classList.remove('opening');
                }, 400);
                setTimeout(function () {
                    self.$refs.viewer.classList.add('viewer-in');
                    document.getElementsByTagName('body')[0].classList.add("popup-noscroll");
                    self.isShowedViewer = true;
                    self.$refs.viewer.querySelector('.hooper-track').focus();
                }, 10);
            },
            slideNext() {
                this.$refs.viewerHooper.slideNext();
            },
            slidePrev() {
                this.$refs.viewerHooper.slidePrev();
            },
        },

    }
</script>
