<template>
    <div>
        <div class="popup__content">
            <div class="text-size-l text-bold text-color mb24">Категория</div>
            <div class="input-select" style="width: 100%">
                <input-select-new v-model="form.topSection.value" :options="topSectionsOptions" :placeholder="'Макрораздел'" :list-style="{'max-height': 'none'}" :disabled="false"></input-select-new>
            </div>
                <div style="width: 100%;text-transform: uppercase;" class="mt16">
                    <input-styled
                    :auto-focus="true"
                    name="category"
                    :placeholder="'Название категории'"
                    :placeholderInput="'Название'"
                    @enter="send"
                    v-model="form.category.text"
                    @focus="form.category.error=false"
                    :maxLengthNum="24"
                    :is-error="form.category.error">
                    </input-styled>
                </div>
            <div class="row-b mt24">
                <div class="row-b__col elem-pos-fix mr24">
                    <submit-btn class="btn btn--ico" @click.native="send" :type="form.btnType">
                        <span v-if="form.btnType=='normal'" class="i i-ok7 mr8"></span>Сохранить
                    </submit-btn>
                </div>
            </div>
            <div class="remove-rule" @click="removeCat" v-if="section.name">
                <a href="javascript:void(0);" class="link-icon link-icon__modal">
                    <span class="i i-trash2"></span>
                    <span class="text">Удалить категорию</span>
                </a>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import InputStyled from '@/components/forms/InputStyled.vue';
    import SubmitBtn from '@/components/forms/SubmitBtn.vue';
    import InputSelectNew from '@/components/forms/InputSelectNew.vue';

    export default {
        name: "RuleCategory",
        components: {
            InputStyled,
            SubmitBtn,
            InputSelectNew,
        },
        mounted(){
            //if(this.section && this.section.length>0){
                this.form.category.text = this.section.name;
                this.form.topSection.value = this.activeTopSection;
            //}
        },
        data() {
            return {
                form:{
                    topSection: {
                        value: '',
                        error: false,
                    },
                    category: {
                        text: '',
                        error: false,
                    },
                    btnType: 'normal'
                },
            }
        },
        methods: {
            removeCat: function(){
                this.$store.dispatch('rules/removeSection', {
                id: this.section.id,
                topSection: this.activeTopSection,
                }).then(result => {
                    this.$store.commit('popups/close', {name: this.popups.ruleCategory.name});
                    if(result){
                        if(this.activeSection == this.section.id){
                            this.$router.push('/sid'+this.activeTopSection+'/sub'+this.sections[0].id+'/');
                        }
                    }
                    else{
                        this.openRemoveCatError();
                    }
                });
            },
            openRemoveCatError: function () {
                //this.$store.commit('popups/close', {name: this.popups.ruleCategory.name});
                this.$store.commit('popups/open', {name: this.popups.removeCatError.name});
            },
            send(){
                let error = false;
                if(!this.form.category.text || this.form.category.text.length == 0){
                    if(this.form.category.error){
                        this.form.category.error = false;
                        setTimeout(() => {
                            this.form.category.error = true;
                        }, 10);
                    }else{
                        this.form.category.error = true;
                    }
                    error = true;
                }
                let section = this.section.id;
                if(!error){
                    this.form.btnType = 'load';
                    this.$store.dispatch('rules/setSection', {
                        name: this.form.category.text,
                        id: this.section.id ? this.section.id : false,
                        topSection: this.form.topSection.value,
                        activeTopSection: this.activeTopSection,
                        isTop: false,
                    }).then((id)=>{
                        this.form.btnType = 'normal';
                        this.$nextTick(()=>{
                            setTimeout(()=>{
                                // На всякий случай очистим данные для редактирования раздела
                                this.$store.commit('rules/setEditSection',[]);
                                if(section){
                                    this.$store.commit('popups/close', {name: this.popups.ruleCategory.name});
                                    this.$router.push({path: '/sid'+this.form.topSection.value+'/sub'+section+'/'});
                                } else if(id) {
                                    this.$store.commit('popups/close', {name: this.popups.ruleCategory.name});
                                    this.$router.push({path: '/sid'+this.form.topSection.value+'/sub'+id+'/'});
                                }
                            }, 200);
                        })
                    });
                }
            },
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                section: state => state.rules.editSection,
                sections: state => state.rules.sections,
                topSections: state => state.rules.topSections,
            }),
            topSectionsOptions() {
              let options = [];

              for(let i in this.topSections) {
                if (this.topSections[i].rights === 'W') {
                  options.push(this.topSections[i]);
                }
              }

              return options;
            },
            activeSection(){
                return this.$route.params.subid ? this.$route.params.subid : this.sections[0].id;
            },
            activeTopSection(){
                //if(this.isSearch) return false;
                return this.$route.params.id ? this.$route.params.id : this.topSections[0].id;
            },
        },
    }
</script>

