<template>
<div class="aside__sticky" id="aside-sticky">
    <div class="nav nav--city" v-if="!isLoad && sections.length>0">
        <div class="nav-wrapper">
            <template v-if="user.access_rights.is_owner || curTopSection.rights == 'W'">
                <draggable :forceFallback="true" :disabled="!canDrag || popups.addRule.show"
                            element="ul"
                           v-model="sections"
                           @start="dragStart"
                           @end="dragSave"
                           :move="dragStatus"
                           :animation="150"
                           :touchStartThreshold="3"
                           :delay="10"
                           class="nav-list"
                >
                <li class="nav-list-item" v-for="(cat, index) in sections" :key="cat.id">
                    <router-link :href="'/rules/sid'+activeTopSection+'/sub'+cat.id+'/'" :to="'/sid'+activeTopSection+'/sub'+cat.id+'/'" class="nav-list-item-link" :class="{active: cat.id == activeSection, 'can-hover': cat.id == activeSection && hoverClass}">
                        <div class="update-notify" v-if="!isEditing && cat.updated && cat.id != activeSection"></div>
                        <span class="cat-name">{{ cat.name }}</span>
                        <span class="i-wrapper">
                            <span class="i i-pencil2" @click.prevent="openRuleCategory(cat)"></span>
                            <span class="i i-pencil2-active" @click.prevent="openRuleCategory(cat)"></span>
                        </span>
                    </router-link>
                </li>
                </draggable>

            </template>
            <ul class="nav-list" v-else>
                <li class="nav-list-item" v-for="(cat, index) in sections" :key="cat.id">
                    <router-link :href="'/rules/sid'+activeTopSection+'/sub'+cat.id+'/'" :to="'/sid'+activeTopSection+'/sub'+cat.id+'/'" class="nav-list-item-link" :class="{active: cat.id == activeSection}">
                        <div class="update-notify" v-if="!isEditing && cat.updated && cat.id != activeSection"></div>
                        <span class="cat-name">{{ cat.name }}</span>
                        <span v-if="curTopSection.rights == 'W'" class="i i-pencil2" @click.prevent="openRuleCategory(cat)"></span>
                    </router-link>
                </li>
            </ul>
        </div>
    </div>
    <div class="add-section__wrap" v-if="(curTopSection.rights == 'W' || user.access_rights.is_owner) && !popups.addRule.show">
        <div class="add-section" @click="openRuleCategory">
            <span class="i i-plus8"></span>
            <span class="i i-plus8-active"></span>
        </div>
    </div>
</div>
</template>

<script>
    import {mapState} from 'vuex';
    import draggable from '@/helpers/vuedraggable.js';

    export default {
        name: "RulesSections",
        components: {
            draggable
        },
        created() {},
        mounted() {
            this.fetchData();
        },
        data(){
            return {
                drag: false,
                disabled: false,
                hoverClass: false,
            }
        },
        computed: {
            ...mapState({
                isLoad: state => state.rules.isLoadSections,
                popups: state => state.popups.items,
                isSearch: state => state.rules.isSearch,
                topSections: state => state.rules.topSections,
                user: state => state.users.user,
                deactivatedItems: state => state.rules.deactivatedItems,
                canDrag: state => state.rules.canDrag,
                writerRights: state => state.rules.writerRights,
            }),
            isEditing() {
              return this.popups.addRule.show;
            },
            activeTopSection(){
                if(this.isSearch) return false;
                return this.$route.params.id ? this.$route.params.id : this.topSections[0].id;
            },
            activeSection(){
                if(this.isSearch) return false;
                return this.$route.params.subid ? this.$route.params.subid : this.sections[0].id;
            },
            curTopSection (){
                for (let i in this.topSections) {
                    if(this.topSections[i].id == this.activeTopSection){
                        return this.topSections[i];
                    }
                }
                return {}
            },
            sections: {
                get(){
                    return this.$store.getters['rules/getSections']
                },
                set(value){
                    this.$store.commit('rules/setSections', {
                        items: value
                    });
                }
            }
        },
        methods: {
            dragSave: function (e) {
                if (this.drag) {
                    this.$store.dispatch('rules/setSectionsSort', {});
                    this.drag = false;
                }
            },
            dragStart: function (e) {
            },
            dragStatus: function (e) {
                this.drag = true;
            },
            fetchData () {
                this.$store.dispatch('rules/getSections', {id: this.activeTopSection});
            },
            openRuleCategory: function (cat) {
                this.$store.commit('rules/setEditSection',cat);
                this.$store.dispatch('popups/open', {name: this.popups.ruleCategory.name});
            },
        },
        watch: {
            '$route.params.id': 'fetchData',
        },
    }
</script>

<style scoped>
</style>
