<template>
    <div class="">
        <div class="nothing-found" v-if="!isLoad && items.length === 0 && !isErrorPage && !isSearch">
            <svg class="big-icon" >
                <use xlink:href="@/assets/img/big-icons-sprite.svg#emoji-nothing"></use>
            </svg>
            <div class="text-size-l text-color-grey mt16">
            Здесь пока ничего нет...
            </div>
        </div>

        <draggable :forceFallback="true" :disabled="!canDrag || popups.addRule.show"
                    element="div"
                   v-model="items"
                   @start="dragStart"
                   @end="dragSave"
                   :move="dragStatus"
                   :animation="150"
                   :touchStartThreshold="3"
                   :delay="10"
                   class="rules"
                   :class="{'dragging': drag}"
                   v-else-if="!isErrorPage"
                   >
                   <!--updated: index == 3,-->
            <div class="rule" v-for="(item, index) in items" :id="'rule'+item.id" :key="'item_'+item.id" :class="{rulepreloader: item.texLoading, highlighted:  item.opened && item.id == highlightedId , opened: item.opened, editor: curTopSection && curTopSection.rights == 'W' || user.access_rights.is_owner, updated: (item.updated || isRemovedPage) && !(item.finally && user.access_rights.is_owner), ready: item.finally && user.access_rights.is_owner}">
                <div class="rule__sections" v-if="isRemovedPage && item.sections_before.length">
                    <span :class="{'line-through': item.sections_before[0].removed}" v-html="item.sections_before[0].name"></span>
                    <span class="text-color-grey ml4 mr4">/</span>
                    <span :class="{'line-through': item.sections_before[1].removed}" v-html="item.sections_before[1].name"></span>
                </div>
                <div class="rule__sections" v-else-if="isSearch && item.sectionTopName && item.sectionName">
                    <span v-html="item.sectionTopName"></span>
                    <span class="text-color-grey ml4 mr4">/</span>
                    <a href="" @click.prevent="goToRule('/sid'+item.sectionTop+'/sub'+item.section+'/#rule'+item.id)" v-if="isSearch" v-html="item.sectionName"></a>
                    <span v-else v-html="item.sectionName"></span>
                </div>
                <div class="rule-top" @click="toggleRule(index, item)" :id="'rule_'+item.id"></div>
                <div class="rule__preloader-wrapper">
                    <div class="rule__preloader"></div>
                </div>
                <svg v-if="item.isCheckEdit" class="btn-preloader">
                    <use xlink:href="@/assets/img/big-icons-sprite.svg#preloader"></use>
                </svg>
                <div v-else-if="(getRights(item) === 'W'  && !item.finally)  && !isRemovedPage || user.access_rights.is_owner && !isRemovedPage" class="edit" @click="openAddRule(item)">
                    <span class="i i-pencil2"></span>
                    <span class="i i-pencil2-active"></span>
                </div>
                <div v-else-if="item.finally && !user.access_rights.is_owner" class="edit" @click="openEditAdminDisabled()">
                    <span class="i i-lock3"></span>
                </div>

                <span class="copy" v-if="!isRemovedPage" @click="copyRule($event, item)">
                    <span class="i i-copy2"></span>
                    <span class="i i-copy2-filled"></span>
                    <span class="i i-ok4"></span>
                </span>

                <span class="recover" @click="openRecover(item)" v-if="isRemovedPage">
                    <span class="i i-refresh4"></span>
                </span>

                <span class="delete" @click="openRemoveRule(item)" v-if="isRemovedPage">
                    <span class="i i-trash2"></span>
                    <span class="i i-trash2-active"></span>
                </span>
                <!-- <div class="text-size-l text-bold">
                    {{item.name}}
                </div>
                <div class="rule__text mt8" v-html="item.text.replace(/(?:\r\n|\r|\n)/g, '<br />')"></div> -->
                <div class="rule__text-container">
                    <div class="rule__text">
                        <span v-html="item.name"></span>
                        <dynamic-html v-if="item.text" :opened="item.opened" :html="item.text" class="rule__text-full"></dynamic-html>
                    </div>
                </div>
            </div>
        </draggable>
        <div class="preloader-page" v-if="isLoad">
            <div class="preloader"></div>
        </div>
        <!-- <div class="rules --ghost" v-if="isLoad">
            <div class="rule" v-for="i in 6">
                <span></span>
            </div>
        </div> -->

      <popup :is-set-styled="false" :show="popups.recoverRule.show" :name="popups.recoverRule.name" :width="424" :add-class="'recover-modal'">
        <recover-rule :data="recoverItem"></recover-rule>
      </popup>
    </div>
</template>

<script>
    import {mapState} from 'vuex';
    import Gallery from '@/components/Gallery.vue';
    import draggable from '@/helpers/vuedraggable.js';
    import { copyTextToClipboard } from "@/helpers/util/helper";
    import {scrollTo} from '@/helpers/animate.js';
    import DynamicHtml from '@/components/moderator/Rules/DynamicHtml.vue';
    import Popup from '@/components/modal/Popup.vue';
    import RecoverRule from '@/components/modal/rules/RecoverRule.vue';

    export default {
        name: "List",
        components: {
            Gallery,
            draggable,
            DynamicHtml,
            Popup,
            RecoverRule
        },
        created() {},
        mounted() {
            window.addEventListener('scroll', this.handleScroll);

            if(this.$route.hash !== ""){
                this.fetchData().then(()=>{
                    this.$nextTick(()=>{
                        scrollTo(this.$route.hash);
                        for (let itemIndex in this.items) {
                            if(this.$route.hash.replace('#rule','') == this.items[itemIndex].id){
                                this.toggleRule(itemIndex, this.items[itemIndex]);
                                this.highlightedId = this.items[itemIndex].id
                            }
                        }
                    })
                });
            }
            else{
                this.fetchData();
            }

            this.$store.subscribe((mutation, state) => {
                if(mutation.type=='rules/toggleRolled'){
                    for (let itemIndex in this.items) {
                        if((this.rolled && !this.items[itemIndex].opened) || (!this.rolled && this.items[itemIndex].opened)){
                            this.toggleRule(itemIndex, this.items[itemIndex]);
                        }
                    }
                }
            });
        },
        props: {
            isErrorPage: {
                default: false
            }
        },
        data(){
            return {
                drag: false,
                canToggle: true,
                highlightedId: false,
                recoverItem: {}
            }
        },
        computed: {
            ...mapState({
                isLoad: state => state.rules.isLoadItems,
                sections: state => state.rules.sections,
                popups: state => state.popups.items,
                isSearch: state => state.rules.isSearch,
                rolled: state => state.rules.rolled,
                topSections: state => state.rules.topSections,
                searchStr: state => state.rules.searchStr,
                canDragState: state => state.rules.canDrag,
                user: state => state.users.user,
            }),
            canDrag(){
                if(this.user.access_rights.is_owner || (this.curTopSection && this.curTopSection.rights == 'W')) {
                    return this.canDragState
                } else {
                    return false
                }
            },
            activeTopSection(){
                //if(this.isSearch) return false;
                if(this.topSections[0]){
                    return this.$route.params.id ? this.$route.params.id : this.topSections[0].id;
                }
                else
                    return false;
            },
            activeSection(){
                return this.$route.params.subid ? this.$route.params.subid : (this.sections[0])?this.sections[0].id:false;
            },
            items: {
                get(){
                    return this.$store.getters['rules/getItems']
                },
                set(value){
                    this.$store.commit('rules/setItemsLocal', {
                        items: value
                    });
                }
            },
            curTopSection (){
                if(this.topSections[0]){
                    for (let i in this.topSections) {
                        if(this.topSections[i].id == this.activeTopSection){
                            return this.topSections[i];
                        }
                    }
                    return {};
                }
                else
                    return {};
            },
            isRemovedPage (){
                return this.$route.name == 'rules-removed';
            }
        },
        methods: {
            openRecover: function (item) {
              this.recoverItem = item;
              this.$store.commit('popups/open', {name: this.popups.recoverRule.name});
            },
            openRemoveRule: function (item) {
                //this.$store.commit('popups/close', {name: this.popups.addRule.name});
                if(this.isRemovedPage){
                    this.$store.dispatch('rules/setEditItem',item).then(()=>{
                        this.$store.commit('popups/open', {name: this.popups.removeRuleForever.name});
                    });
                }
                else{
                    this.$store.dispatch('rules/setEditItem',item).then(()=>{
                        this.$store.commit('popups/open', {name: this.popups.removeRule.name});
                    });
                }
            },
            copyRule(e, item){
                let text = process.env.VUE_APP_PATH_FRONT+"/rules/sid"+item.sectionTop+'/sub'+item.section+"/#rule"+item.id;
                let elem = e.target.classList.contains('i') ? e.target.parentElement : e.target;
                if(e.target.classList.contains('i')){
                    e.target.parentElement.classList.add('copied');
                    setTimeout(function(){
                        e.target.parentElement.classList.remove('copied');
                    }, 500);
                }else{
                    e.target.classList.add('copied');
                    setTimeout(function(){
                        e.target.classList.remove('copied');
                    }, 500);
                }
                copyTextToClipboard(text);
            },
            dragSave: function (e) {
                if (this.drag) {
                    this.$store.dispatch('rules/setRulesSort', {isRemoved: this.$route.name == 'rules-removed'});
                    this.drag = false;
                    console.log('dragSave');
                    setTimeout(()=>{
                        this.canToggle = true;
                        console.log('canToggle');
                    }, 400);
                }
            },
            dragStart: function (e) {
            },
            dragStatus: function (e) {
                this.drag = true;
                this.canToggle = false;
            },
            goToRule(link){
                this.$store.commit('rules/setIsSearch', false);
                this.$router.push(link);
            },
            toggleRule(index, item){
                if(this.canToggle){
                    this.$store.dispatch('rules/getRuleText',{item: item, index: index, removed: this.isRemovedPage}).then(()=>{
                        let height = document.getElementById("rule_"+item.id).parentElement.querySelector('.rule__text').clientHeight;
                        document.getElementById("rule_"+item.id).parentElement.querySelector('.rule__text-container').style.maxHeight = document.getElementById("rule_"+item.id).parentElement.classList.contains('opened') ? '17px' : (height + 30) + 'px';
                        this.$store.commit('rules/toggleRule', index);
                        if(item.updated){
                            this.$store.dispatch('rules/deleteMarks', {id:item.id});
                        }
                    });
                }
                this.highlightedId = false;
            },
            getRights(item) {
              let section = this.topSections.find(sect => sect.id === item.sectionTop),
                  rights = 'R';

              if (typeof(section) !== 'undefined') {
                rights = section.rights;
              }

              return rights;
            },
            fetchData () {
                if(this.activeSection || this.isRemovedPage){
                    this.highlightedId = false;
                    let full = false;
                    if(this.$route.hash !== ""){
                        full = true;
                    }

                    let id = this.activeSection;
                    if(this.isRemovedPage){ id = 'removed';}

                    return new Promise((resolve, reject) => {
                        this.$store.dispatch('rules/getItems', {id: id, reload: true, full: full, topSect: this.activeTopSection}).then(()=>{
                            resolve();
                        });
                    });
                }
            },
            openRuleCategory: function (cat) {
                this.$store.commit('rules/setEditSection',cat);
                this.$store.dispatch('popups/open', {name: this.popups.ruleCategory.name});
            },
            handleScroll: function () {
                let scroll = window.scrollY;
                let viewport = window.innerHeight;
                let bodyHeight = document.body.clientHeight;
                if(bodyHeight-80 <= scroll + viewport){
                    this.$store.dispatch('rules/getItems', {
                        id: this.activeSection
                    })
                }
            },
            openEditAdminDisabled: function () {
              this.$store.dispatch('popups/open', {name: this.popups.editAdminDisabled.name});
            },
            openAddRule: function (item) {
              this.$emit('openeditrule')
              if (this.isSearch) {
                this.$store.commit('rules/setIsSearch', false);
                this.$store.commit('rules/setIsSearchPrev', true);
              } else {
                  this.$store.commit('rules/setPreviousSection', {topSection: this.activeTopSection, section: this.activeSection});
              }

              if(this.$route.path !== '/sid'+item.sectionTop+'/sub'+item.section+'/'){
                this.$router.push('/sid'+item.sectionTop+'/sub'+item.section+'/');
              }

              //очистка от <span class="query"></span>
              let tmpDiv = document.createElement("div");
              tmpDiv.innerHTML = item.name;
              item.name = tmpDiv.innerText;

              if(!item.isCheckEdit){
                    let timeout = setTimeout(()=>{
                        this.$store.commit('rules/setCheckEditLoader',{id:item,value: true});
                    },500)

                    this.$store.commit('rules/setLoadItems', true);
                    this.$store.dispatch('rules/checkEditItem',item).then((result)=>{
                        if(result.status){
                            this.$store.commit('rules/setLoadItems', false);
                            this.$store.commit('popups/open', {name: this.popups.editDisabled.name});
                        } else {
                            if(this.$route.name == 'rules-removed'){
                                this.$router.push('/sid'+item.sectionTop+'/sub'+item.section+'/');
                            }

                            this.$store.dispatch('rules/setEditItem',item).then(()=>{
                              this.$store.dispatch('popups/open', {name: this.popups.addRule.name});
                            })
                            document.documentElement.scrollTop = 0;

                        }
                        clearTimeout(timeout);
                        this.$store.commit('rules/setCheckEditLoader',{id:item,value: false});
                    },()=>{
                        this.$store.commit('rules/setCheckEditLoader',{id:item,value: false});
                    }).catch(()=>{
                        this.$store.commit('rules/setCheckEditLoader',{id:item,value: false});
                    })
                }

            },
            openSearchField: function () {
                setTimeout(() => {
                    let searchFieldInput = document.getElementById('searchFieldInput');
                    this.$store.commit('rules/openSearchField', true);
                    searchFieldInput.focus();
                }, 100);
            },
            htmlText(text){
                return text.replace(/(?:\r\n|\r|\n)/g, '<br />');
            },
        },
        watch: {
            '$route': 'fetchData'
        },
        beforeDestroy() {
            window.removeEventListener('scroll', this.handleScroll);
        },
    }
</script>
<style scoped>
.rulepreloader{
}
</style>
