<template>
    <div>
        <div class="add-rule__form" v-if="popups.addRule.show">
            <div>
                <input-styled
                        v-if="item.id"
                        name="title"
                        :placeholder="'Заголовок'"
                        :placeholderInput="'Заголовок'"
                        @input="$emit('change')"
                        v-model="form.title.value"
                        @focus="form.title.error=false"
                        :is-error="form.title.error"
                        :maxLengthNum="85">
                </input-styled>
                <input-styled
                        v-else
                        name="title"
                        :auto-focus="true"
                        @input="$emit('change')"
                        :placeholder="'Заголовок'"
                        :placeholderInput="'Заголовок'"
                        v-model="form.title.value"
                        @focus="form.title.error=false"
                        :is-error="form.title.error"
                        :maxLengthNum="65">
                </input-styled>
                <div class="textarea-field mt16" :class="{'error': form.text.error, 'repeat-error': repeatTextareaError}">
                    <div class="editor">

                        <span class="i i-info3"></span>
                        <editor-content @paste.stop.prevent @showimage="showImage" class="inp" :editor="editor" />
                    </div>
                    <div class="bottom-text">
                        не менее 10-ти символов
                    </div>
                </div>
            </div>

            <div class="add-rule__footer" v-if="item.id" style="margin-top: 400px;">
                <div class="add-rule__info" v-if="historyLast.date">
                    <tooltip
                         :direction="'top-left-direction'"
                         :add-class-wrap="'tooltip-log'"
                         :isBtnClose="false"
                         :add-style="{width: 360 + 'px'}"
                         @tooltipshow="logActive = true"
                         @tooltipclose="logActive = false">
                        <span slot="tooltipBtn" class="i i-info mr4" :class="{active: logActive}">
                        </span>

                        <div slot="tooltipContent">
                            <div class="text-size-s text-bold">
                                История
                            </div>
                            <div class="mt8" v-for="row in history">
                                <span>{{ row.date }}, {{ row.time }}</span>
                                <span class="text-color-grey ml8 mr8">|</span>
                                <span>{{ row.event }}</span>
                                <span class="ml8 mr8 text-color-grey">|</span>
                                <span>{{ row.user }}</span>
                                <span class="ml8 i i-notify" v-if="row.notify"></span>
                                <span class="ml8 i i-notify-mute i--dark-trans" v-else></span>
                            </div>
                        </div>
                    </tooltip>
                    <span>{{ historyLast.date }}, {{ historyLast.time }}</span>
                    <span class="text-color-grey ml8 mr8">|</span>
                    <span>{{ historyLast.event }}</span>
                    <span class="ml8 mr8 text-color-grey">|</span>
                    <span>{{ historyLast.user }}</span>
                    <span class="ml8 i i-notify" v-if="historyLast.notify"></span>
                    <span class="ml8 i i-notify-mute i--dark-trans" v-else></span>
                </div>
            </div>
        </div>
        <div class="viewer-container viewer-backdrop viewer-fixed viewer-hooper viewer-fade" ref="viewerAddRule"
             @keyup.space="slideNext" @keyup.esc="closeViewer">

            <div class="viewer-canvas-wrapper">

                <div class="viewer-canvas" :class="{'nav-hovered-prev': hoveredPrev, 'nav-hovered-next': hoveredNext}">
                    <hooper @slide="slide" class="carousel-thumbs viewer-carousel" ref="viewerHooperAddRule" :infiniteScroll="true">

                        <slide :key="index"
                               v-for="(item,index) in files">
                            <div class="item" v-if="item.mediaType == 'photo'">
                                <img  v-lazy="item.host+item.path" alt="" class="js-img"  v-if="item.host && files != [] && (currentSlide == index+1 || currentSlidePrev == index) && isShowedViewer">
                                <img v-lazy="'https://rules.su/'+item.path" alt=""  v-else-if="files != [] && (currentSlide == index+1 || currentSlidePrev == index) && isShowedViewer">
                                <div class="inner-nav" v-if="files.length > 1">
                                    <div class="prev" @click="slidePrev" @mouseover="hoveredPrev = true" @mouseleave="hoveredPrev = false"></div>
                                    <div class="next" @click="slideNext"  @mouseover="hoveredNext = true" @mouseleave="hoveredNext = false"></div>
                                </div>
                            </div>
                            <div class="item" v-else>
                                <iframe width="1100"
                                      height="450"
                                      v-if="files != [] && (currentSlide == index+1 || currentSlidePrev == index) && isShowedViewer"
                                      :src="'https://www.youtube.com/embed/'+item.id+'?enablejsapi=1'"
                                      frameborder="0"
                                      allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
                                      allowfullscreen></iframe>
                                <div class="inner-nav"  v-if="files.length > 1">
                                    <div class="prev" @click="slidePrev" @mouseover="hoveredPrev = true" @mouseleave="hoveredPrev = false"></div>
                                    <div class="next" @click="slideNext"  @mouseover="hoveredNext = true" @mouseleave="hoveredNext = false"></div>
                                </div>
                            </div>
                        </slide>
                        <hooper-navigation slot="hooper-addons"  v-if="files.length > 1"></hooper-navigation>
                    </hooper>
                </div>
                <div role="button" class="viewer-button viewer-close" @click="closeViewer"></div>
                <div class="photo-num">
                    {{currentSlide}}
                </div>
            </div>
        </div>
        <popup :is-set-styled="false" :show="popups.recoverRule.show" :name="popups.recoverRule.name" :width="512" >
            <div class="popup-info popup-icon-info">
                <svg style="height: 48px;">
                    <use xlink:href="@/assets/img/big-icons-sprite.svg#recover"></use>
                </svg>

                <div class="text mt24">
                    <div class="text-size-l text-bold info-title">Восстановить правило?</div>

                    <p class="info-text mt16">
                        Оно вернётся в категорию, из которой было удалено.
                    </p>
                </div>

                <div class="mt24">
                    <submit-btn @click.native="send()" :type="form.btnType"><span v-if="form.btnType=='normal'" class="i i-ok7 mr8"></span>Подтвердить</submit-btn>
                </div>
            </div>
        </popup>
    </div>
</template>

<script>


import {mapState} from 'vuex'
import InputStyled from '@/components/forms/InputStyled.vue';
import Popup from '@/components/modal/Popup.vue';
import TextareaField from '@/components/forms/TextareaField.vue';
import InputSelect from '@/components/forms/InputSelect.vue';
import Tooltip from '@/components/Tooltip.vue';
import SubmitBtn from '@/components/forms/SubmitBtn.vue'
import TextEditorNew from '@/components/editor/TextEditorNew.vue'
import draggable from '@/helpers/vuedraggable.js';
import {Hooper, Slide, Navigation as HooperNavigation} from 'hooper'; // carousel
import { Editor, EditorContent, EditorMenuBar } from 'tiptap';
import InputSelectNew from '@/components/forms/InputSelectNew.vue';
import {scrollTo} from '@/helpers/animate.js';
import {
    HardBreak,
    Image,
    Placeholder,
    History,
} from 'tiptap-extensions'
import ScreenShot from '@/components/editor/tiptop/screenshot.js'
import Youtube from '@/components/editor/tiptop/youtube.js'

    export default {
        name: "AddRule",
        components: {
            InputStyled,
            TextareaField,
            InputSelect,
            SubmitBtn,
            draggable,
            Hooper,
            Slide,
            HooperNavigation,
            TextEditorNew,
            EditorContent,
            Tooltip,
            EditorMenuBar,
            Popup,
            InputSelectNew
        },
        props: {
            regionsProp: {},
            show: {
                default: function () {
                    return false
                }
            },
            name: '',
            editor: {},
            focused: {
                default: function(){
                    return false
                }
            },
            inputText: '',

        },
        data: function () {
            return {
                regions: {},
                countSelect: 0,
                init: false,
                openAside: false,
                button: 'normal',
                loadFiles: false,
                currentSlide: 1,
                currentSlidePrev: 1,
                isShowedViewer: false,
                secondSections: [],
                hoveredPrev: false,
                hoveredNext: false,
                logActive: false,
                isSave: false,
                form:{
                    text:{
                        value: "",
                        error: false,
                    },
                    section:{
                        value: "",
                        error: false,
                    },
                    category:{
                        value: "",
                        error: false,
                    },
                    title: {
                        value: '',
                        error: false,
                    },
                    topSection: {
                        value: '',
                        error: false,
                    },
                    screen:[],
                    screenError:[false, false, false],
                    btnType: 'normal',
                    fileInput: [],
                    tmpFiles:  [],
                    tmpFilesAppend: [],
                    removeFiles: [],
                    filesDragRun: false,
                },
                fieldName: 'file',
                appPath: process.env.VUE_APP_PATH_BACK,
                repeatTextareaError: false,
            }
        },
        created() {
            window.addEventListener('beforeunload', this.handlerBeforeUnload)
        },
        mounted: function () {
            document.addEventListener('showimage', this.showImage);
            document.addEventListener('showvideo', this.showImage);
            document.addEventListener('keyup', this.keyUp);
            this.$store.dispatch('rules/setTimerBlockEdit', {id:this.item.id});
            this.setItem();

            //document.getElementsByTagName('body')[0].classList.add("noscroll");
        },
        beforeDestroy() {
            if(!(this.blockEdit && this.blockEdit.status)){
                this.$store.dispatch('rules/clearBlockEdit', {id:this.item.id});
            }
            if(this.item.id || this.isSave){
                this.$emit('scroll',this.item.id ? this.item.id: 'new');
            }
            document.removeEventListener('showimage', this.showImage );
            document.removeEventListener('showvideo', this.showImage );
            document.removeEventListener('keyup', this.keyUp);
            window.removeEventListener('beforeunload', this.handlerBeforeUnload);
            this.$store.commit('rules/tumblerOff', this.tumblers.notify.name);
            this.$emit('close');
        },
        watch: {
            focused(){
                this.form.text.error = false;
            },
            inputText(){
                this.form.text.value = this.inputText
            },
            show: function() {},
            'form.text.error'(val, oldVal){
                if(oldVal){
                    this.repeatTextareaError = true;
                }
            },
            'editor.focused'(val){
                if(val){
                    this.repeatTextareaError = false;
                }
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                item: state => state.rules.editItem,
                sections: state => state.rules.sections,
                selectSections: state => state.rules.selectSections,
                topSections: state => state.rules.topSections,
                tumblers: state => state.rules.tumblers,
            }),
            history() {
              let history = [];

              if (typeof this.item.history !== 'undefined') {
                  history = this.item.history;
              }

              return history;
            },
            historyLast() {
              let history = [];

              if (typeof this.item.history !== 'undefined' && this.item.history.length) {
                  history = this.item.history.slice(-1).pop();
              }

              return history;
            },
            activeTopSection(){
                if(this.isSearch) return false;
                return this.$route.params.id ? this.$route.params.id : this.topSections[0].id;
            },
            activeSection(){
                return this.$route.params.subid ? this.$route.params.subid : this.sections[0]?this.sections[0].id : '';
            },
            files: {
                /*if(this.item.files && this.item.files.length>0){
                    return this.item.files.concat(this.form.tmpFiles)
                }
                else
                    return this.form.tmpFiles;*/
                get(){
                    let files = this.$store.getters['rules/getItemFiles'];
                    return files ? files : [];
                },
                set(value){
                    this.$store.commit('rules/setItemFiles', {
                        items: value
                    });
                }
            },
            isRemovedPage (){
                return this.$route.name == 'rules-removed';
            }
        },

        methods: {
            // setImage(){
            //     let src = document.querySelector('.is-active').querySelector('.js-img').getAttribute('data-src');
            //     document.querySelector('.is-active').querySelector('.js-img').setAttribute('src', '' + src);
            //     console.log(document.querySelector('.is-active').querySelector('.js-img'));
            // },

            setItem() {
              let data = {...this.item};
              data.text = '';
              this.$store.dispatch('rules/getRuleText',{item: data, removed: this.isRemovedPage}).then((item)=>{
                this.$store.dispatch('rules/setEditItem', item).then(() => {
                  if (this.item.name) {
                    this.form.title.value = this.item.name.replace(/(&nbsp;)/g, ' ');
                  }
                  if (this.item.section) {
                    this.form.section.value = this.getSectionName(this.item.section);
                  } else {
                    this.form.section.value = this.getSectionName(this.activeSection);
                  }

                  this.secondSections = this.sections;
                  this.form.topSection.value = this.activeTopSection;
                  this.form.category.value = this.activeSection;


                  this.$store.commit('rules/setItemFiles', {items: []});


                  if (this.item.text) {
                    this.form.text.value = this.item.text.replace(/(&nbsp;)/g, ' ');
                  }

                  setTimeout(() => {
                    if (this.form.text.value) {
                      if (this.form.text.value.indexOf('<p') === -1) {
                        this.editor.setContent('<p>' + this.form.text.value.replace(/(&nbsp;)/g, ' ').replace(/(?:\r\n|\r|\n)/g, '<br />') + '<p>')
                      } else {
                        this.editor.setContent(this.form.text.value.replace(/(&nbsp;)/g, ' ').replace(/(?:\r\n|\r|\n)/g, '<br />'))
                      }
                      this.editor.focus('start');
                      this.updateImages(this.form.text.value);
                    }
                    this.$store.commit('rules/setLoadItems', false);
                  }, 50);
                });
              });
            },
            handlerBeforeUnload(e){
                e.returnValue = null;
                return null;
            },
            updateImages(text){
                if(text){
                    let result = text.match(/<span[\s\S]*?class="(image|youtube)"[\s\S]*?\/span>/gi);
                    let IDs = [];
                    let files = [];
                    for(let r in result){
                        let div = document.createElement('div');
                        div.innerHTML = result[r];
                        let type;
                        let element;
                        if(div.querySelector("span.image")){
                            element = div.querySelector("span.image");
                            type = 'image';
                        }
                        if(div.querySelector("span.youtube")){
                            element = div.querySelector("span.youtube");
                            type = 'youtube';
                        }
                        //if(element && IDs.indexOf(element.dataset.id) === -1){
                        IDs.push(element.dataset.id);
                        if(type === 'image'){
                            IDs.push(element.dataset.id);
                            let host = element.dataset.href.indexOf('https://zems.pro') > -1 ? 'https://zems.pro' : element.dataset.href.indexOf('https://rules.su') > -1 ? 'https://rules.su' : false;
                            let href = element.dataset.href.replace('https://zems.pro','').replace('https://rules.su','');
                            this.$store.commit('rules/addFile',{id:element.dataset.id,path:href,icon:href, mediaType: 'photo', host: host});

                        }
                        if(type === 'youtube'){
                            let id = element.dataset.id;
                            let path = '//img.youtube.com/vi/' + id + '/maxresdefault.jpg'
                            this.$store.commit('rules/addFile',{id: id, path: path, icon: path, mediaType: 'video'});
                        }
                        //}

                    }
                    return files;
                }
            },

            getSubsections(event){
                this.$store.dispatch('rules/getSubsectionOption', {id: event}).then((result)=>{
                    //console.log(result);
                    this.secondSections = result;
                    //this.form.category.value = 289;
                    if(result.length>0){
                        //console.log(result[0].id);
                        this.form.category.value = result[0].id;
                    }
                });
            },
            getRandomIntInclusive(min, max) {
                min = Math.ceil(min);
                max = Math.floor(max);
                return Math.floor(Math.random() * (max - min + 1)) + min; //Максимум и минимум включаются
            },

            close: function () {
                if (this.show && this.name) {
                    this.$store.commit('popups/close', {name: this.name});
                    document.getElementsByTagName('body')[0].classList.remove("noscroll")
                }
            },
            slide(payload){
                this.currentSlide = +payload.currentSlide == -1 ? this.files.length : +payload.currentSlide +1;
                if(this.files && +this.currentSlide > this.files.length){
                    this.currentSlide = 1;
                }
                this.currentSlidePrev = +payload.slideFrom
                this.pauseVideos();
            },
            inputFocus(ref) {
                if(!!this.$refs[ref]){
                    this.$nextTick(() => {this.$refs[ref].focus()});
                }
            },
            divPaste(e){
                e.preventDefault();
                let pastedData = e.clipboardData.getData('text');
                document.execCommand('inserttext', false, pastedData);
            },

            removeScreen(i){
                this.$set(this.form.screen, i, '');
                this.form.screenError.splice(i,1,false);
            },
            slideNext() {
                this.$refs.viewerHooperAddRule.slideNext();
            },
            slidePrev() {
                this.$refs.viewerHooperAddRule.slidePrev();
            },
            pauseVideos() {
                [...document.getElementsByTagName('iframe')].forEach(i => i.contentWindow.postMessage('{"event":"command","func":"pauseVideo","args":""}', '*'));
            },
            closeViewer() {
                this.pauseVideos();
                if (this.isShowedViewer) {
                    this.$refs.viewerAddRule.classList.add('viewer-transition');
                    this.$refs.viewerAddRule.classList.remove('viewer-in');
                    document.getElementsByTagName('body')[0].classList.remove("popup-noscroll");
                    let self = this;
                    setTimeout(function () {
                        if (self.$refs.viewerAddRule) {
                            self.$refs.viewerAddRule.classList.remove('viewer-transition');
                        }
                        if (self.$refs.viewerHooperAddRule) {
                            self.$refs.viewerHooperAddRule.restart();
                        }
                        self.isShowedViewer = false;
                    }, 200);
                }
            },
            showViewer(index) {
                this.$refs.viewerAddRule.classList.add('opening');
                this.$refs.viewerHooperAddRule.slideTo(index);
                this.$refs.viewerAddRule.classList.add('viewer-transition');
                this.$refs.viewerAddRule.querySelector('.hooper-track').setAttribute('tabindex', 0);
                let self = this;
                setTimeout(function () {
                    self.$refs.viewerAddRule.classList.remove('viewer-transition');
                    self.$refs.viewerAddRule.classList.remove('opening');
                }, 400);
                setTimeout(function () {
                    self.$refs.viewerAddRule.classList.add('viewer-in');
                    document.getElementsByTagName('body')[0].classList.add("popup-noscroll");
                    self.isShowedViewer = true;
                    self.$refs.viewerAddRule.querySelector('.hooper-track').focus();
                }, 10);
            },
            deleteFileReal(id){
                for(let f in this.item.files){
                    if(this.item.files[f].id === id){
                        this.form.removeFiles.push({'value' :this.item.files[f].value_id, 'id':this.item.files[f].id});
                    }
                }
                this.$store.commit('rules/removeFile', id);
            },
            deleteFileTmp(id){
                for(let f in this.form.tmpFiles){
                    if(this.form.tmpFiles[f].id === id){
                        //if(this.files[f].is_tmp){
                            for(let inpF in this.form.fileInput){
                                if(this.form.fileInput[inpF].id == this.form.tmpFiles[f].input_id){
                                    this.form.fileInput.splice(inpF , 1);
                                }
                            }
                            this.form.tmpFiles.splice(f , 1);
                        //}
                    }
                }
            },
            dragSave: function (e) {
                this.form.filesDragRun = true;
                //console.log(e)
                //this.$emit('input', this.files);
            },
            dragStart: function (e) {
                //console.log(e)
            },
            dragStatus: function (e) {
                //console.log(e)
            },
            sendSave: function () {
                if(this.item.id && !this.item.active){
                    this.$store.dispatch('popups/open', {name: this.popups.recoverRule.name});
                } else {
                    this.send();
                }
            },
            send(){
                let error = false;
                this.repeatTextareaError = false;
                if(!this.form.text.value || this.form.text.value.length == 0 || this.form.text.value.replace('<p>','').replace('</p>','').trim().length == ''){
                    if(this.form.text.error){
                        this.form.text.error = false;
                        setTimeout(() => {
                            this.form.text.error = true;
                        },10);
                    }else{
                        this.form.text.error = true;
                    }
                    error = true;
                }
                if(!this.form.title.value || this.form.title.value.length == 0){
                    if(this.form.title.error){
                        this.form.title.error = false;
                        setTimeout(() => {
                            this.form.title.error = true;
                        },10);
                    }else{
                        this.form.title.error = true;
                    }
                    error = true;
                }
                /*this.form.screen.forEach((item, i, arr) => {
                    if(item!='' && item.match(/prntscr.com|yadi.sk|prnt.sc/) === null) {
                        this.form.screenError.splice(i,1,true);
                        error = true;
                    }
                });*/

              //this.$store.commit('rules/setSelectedSection', true);
              if (this.activeSection) {
                this.$store.commit('rules/setSelectedSection', true);
              } else {
                error = true;
                let speedLight = 200;
                this.$store.commit('rules/setSelectedSection', false);
                setTimeout(() => {
                  this.$store.commit('rules/setSelectedSection', true);
                  setTimeout(() => {
                    this.$store.commit('rules/setSelectedSection', false);
                    setTimeout(() => {
                      this.$store.commit('rules/setSelectedSection', true);
                      setTimeout(() => {
                        this.$store.commit('rules/setSelectedSection', false);
                      }, speedLight);
                    }, speedLight);
                  }, speedLight);
                }, speedLight);
              }

                if(!error){
                    this.form.btnType = 'load';
                    this.$emit('load', 'load');

                    let params = {
                        id: this.item.id ? this.item.id : false,
                    };

                    let formData = new FormData();
                    formData.append('name', this.form.title.value);
                    formData.append('text', this.form.text.value);
                    formData.append('section', this.activeSection);
                    formData.append('notifyInvolved', this.tumblers.notify.value);
                    formData.append('finally', this.tumblers.finally.value);
                    formData.append('type', 'saveRule');

                    params.formData = formData;
                    params.activeSection = this.activeSection;

                    this.$store.dispatch('rules/setRule', params).then((item)=>{
                        this.form.btnType = 'ok';
                        this.$emit('load', 'ok');
                        this.isSave = true;
                        setTimeout(()=>{
                            this.form.btnType = 'normal';
                            this.$emit('load', 'normal');
                            this.$store.commit('popups/close', {name: this.popups.addRule.name});
                            this.$store.commit('popups/close', {name: this.popups.recoverRule.name});

                            //На всякий случай очистим данные для редактирования раздела
                            this.$store.commit('rules/setEditSection',[]);
                        }, 200);
                    });
                }
                //this.$store.commit('popups/close', {name: this.popups.addRule.name});
            },
            getSectionName(id){
                let this_ = this, result;
                if (this.sections.length) {
                  this.sections.forEach(function(item, i, arr) {
                    if(item.id == id){
                      result = item.name;
                      return false;
                    }
                  });
                }

                return result;
            },
            onFileChange: function(e) {
                let self = this;
                let files = e.target.files || e.dataTransfer.files;
                if (!files.length)
                    return;

                for (var i = 0; i < files.length; i++) {
                    let file = files[i];
                    self.form.fileInput.push({id: i,file: file});

                    let URL = window.URL || window.webkitURL;
                    let img = new Image();
                    let file_index = i;
                    img.src = URL.createObjectURL(file);

                    img.id = i;
                    img.onload = function () {
                        self.form.tmpFiles.push({
                            img: this,
                            file: file,
                            path: img.src,
                            icon: img.src,
                            is_tmp: true,
                            input_id: file_index,
                            "mediaType":"photo",
                            id: Math.random().toString(36).substr(2, 9)
                        });
                    };

                }
            },
            pasteEditor(event){
                console.log(event)
            },
            showImage(e){
                this.$store.commit('rules/setItemFiles',{items:[]});
                this.updateImages(this.form.text.value);
                let id = e.detail;
                for(let i in this.files){
                    if(this.files[i].id == id){
                        this.showViewer(i);
                        break;
                    }
                }
            },
            keyUp(e){/*
              if ((e.ctrlKey || e.metaKey) && e.code === 'KeyB') {
                this.$store.commit('popups/open', {name: this.popups.addScreenShot.name});
              }*/
            },
        }
    }
</script>

